.main-slider{

	&__container{
		position: relative;
		padding-top: 190px;
		padding-bottom: 50px;
		margin-bottom: 47px;
		border-bottom: 2px solid $color__grey-lighter;
		@media (max-width:$md2+px) {
			border-bottom: none;
			padding-bottom: 0;
			padding-top: 107px;
		}
		@media (max-width:$md4+px){
			margin-bottom: 0;
			display: flex;
			flex-direction: column;
		}
	}
	&__title{
		margin-bottom: 27px;
	}
}
.slider-main{
	
	&__body{
		min-width: 0;
		position: relative;
		@media (max-width:$md4+px){
			 overflow: visible; 
			 order: 3;
		}
	}
	&__slide{
		position: relative;
		display: flex;
		justify-content: flex-end;
		@media (max-width:$md4+px) {
			flex-direction: column;
		}
	}
	&__content {
		align-self: flex-start;
		position: relative;
		z-index: 2;
		padding: 22px 19px 30px 28px;
		@media (max-width:$md4+px) {
			order: 2;
			top: -75px;
		}
		@media (min-width:$md4+px) {
			padding: 22px 25px 30px 25px;
			left: 0;
			position: absolute;
			@include adaptiv-value ('width', 384, 384, 1);
			@include adaptiv-value ('margin-bottom', 59, 50, 1);
			@include adaptiv-value ('margin-top', 60, 50, 1);
		}
		@media (min-width:$md2+px){
			padding: 47px 40px 50px 30px;
			@include adaptiv-value ('width', 457, 400, 1);
			@include adaptiv-value ('margin-bottom', 207, 140, 1);
			@include adaptiv-value ('margin-top', 107, 90, 1);
		}
	}
	&__navigations{
		@media (max-width:$md2+px){
			position: absolute;
			left: 0;
			bottom: 30px;
			width: 100%;
			padding: 0 20px;
			.slider-arrow {
				font-size: 23px;
			}
		}

		@media (max-width:$md4+px){
			bottom: auto;
			top: 50%;
			transform: translateY(60px);
			.slider-arrow{
				color: $color__white;
				position: relative;
				top: -100px;
			}
		}
		
		@media (max-width:$md6+px){
			transform: translateY(40px);
		}
		
	}

	&__image{
		position: relative;
		flex: 0 1 375px;
		margin: 0 -20px;
		@media (min-width:$md4+px) {
			margin: 0;
			flex: 0 1 656px;
			@include adaptiv-value ('height', 430, 430, 1);
		}
		@media (min-width:$md2+px){
			flex: 0 1 941px;
			@include adaptiv-value ('height', 642, 550, 1);
		}
		
	}
}

.content-main{
	color: $color__white;
	background-color: $color__red;
	

	&__title{
		font-size: 42px;
		letter-spacing: 0.27px;
		line-height: 46px;
		font-weight: 800;
		margin-bottom: 12px;
		@media (max-width:$md2+px){
			margin-bottom: 10px;
		}
	}

	&__text{
		font-size: 18px;
		font-weight: 400;
		letter-spacing: 0.12px;
		margin-bottom: 62px;

		@media (max-width:$md2+px){
			font-size: 16px;
			line-height: 24px;
			margin-bottom: 51px;
		}
	}
	&__button{
		max-width: 290px;
		@media (max-width:$md4+px){
			max-width: 194px;
		}
	}
}

.navigations-slider-main{
	margin-top: 13px;
	display: flex;
	justify-content: space-between;
	@media (max-width:$md4+px){
		margin-top: 0;
	}
}
