
.mediathek{
	.main-content {
		&__title {
			flex: 0 0 300px;
		}
		&__sub-title{
			color: $color__red;
			font-size: 26px;
			padding-bottom: 22px;
			margin-bottom: 22px;
			@media (max-width:$md3+px){
				 padding-bottom: 0;
				 border: none;
			}
		}
		&__column{
			margin-bottom: 50px;
		}
		&__filter{
			padding-bottom: 32px;
			border-bottom: 1px solid $color__grey-lighter;
			@media (max-width:$md3+px){
				 padding-bottom: 13px;
			}
		}
	}
}

.video{
	display: flex;
	margin: 0 -18px;
	@media (max-width:$md3+px){
		margin: 0 -10px;
	}
	@media (max-width:$md4+px){
		display: block;
		margin: 0 -20px;
	}
	.video-card {
		flex: 0 0 50%;
		padding: 0 18px;
		@media (max-width:$md3+px){
			 padding: 0 10px; 
		}
		@media (max-width:$md4+px){
			margin-bottom: 30px;
			padding: 0;
		}
	}
}

.hintergrundmaterial-main{
	display: flex;
	padding: 15px;
	background-color: #f5f3f3;
	&__image{
		position: relative;
		flex: 0 0 280px;
		height: 157px;
		@media (max-width:$md2+px){
			flex: 0 0 265px;
		}
		img{
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			width: 100%;
			object-fit: cover;
		}
	}
	&__body{
		padding-left: 15px;
	}
}
