.contakt {
	padding-top: 200px;
	padding-bottom: 46px;

	@media (max-width:$md2+px) {
		padding-top: 117px;
	}

	&__row {
		display: flex;
	}

	&__side {
		flex: 0 0 263px;
		@media (max-width:$md2+px){
			flex: 0 0 163px;
		}
		@media (max-width:$md3+px) {
			display: none;
		}
	}

	&__main {
		flex: 1 1 auto;
		padding-left: 27px;

		@media (max-width:$md3+px) {
			padding-left: 0;
		}
	}
	&__spollers{
		@media (max-width:$md2+px){
			display: none;
		}
	}

}

.main-contakt {
	@media (min-width:$md2+px){
		display: flex;
	}

	&__column {
		flex: 1 1 auto;
		&:first-child{
			flex: 0 0 34.6%;
			@media (max-width:1100px){
				flex: 0 0 30%;
			}
		}
	}

	&__title {
		margin-bottom: 22px;
		font-weight: 800;
		font-size: 40px;
		letter-spacing: 0.26px;
		line-height: 44px;

		@media (max-width:$md7+px) {
			font-size: 36px;
		}
	}
	&__text {
		font-size: 18px;
		font-weight: 300;
		letter-spacing: 0.12px;
		line-height: 26px;
		margin-bottom: 36px;
	}
	&__sub-title {
		margin-bottom: 29px;
		@media (max-width:$md5+px){
			 font-size: 24px;
			 letter-spacing: 0.17px;
			 margin-bottom: 23px;
			 line-height: 27px;
		}
		@media (max-width:$md7+px){
			font-size: 20px;
		}
	}
	&__item{
		display: flex;
		margin: 0 -15px;
		padding-right: 50px;

		@media (max-width:$md3+px){
			padding-right: 0;
			flex-wrap: wrap;
		}
		@media (max-width:$md5+px){
			 margin: 0; 
		}
	}
	&__card{
		margin-bottom: 60px;
		@media (max-width:$md3+px){
			 margin-bottom: 45px; 
		}
		@media (max-width:$md5+px){
			margin-bottom: 22px;
		}
	}
}

.card-contakt {
	flex: 0 1 50%;
	padding: 0 15px;

	@media (max-width:$md5+px) {
		flex: 1 1 auto;
		padding: 0;
	}

	&__image {
		position: relative;
		width: 100%;
		height: 216px;
		overflow: hidden;
		margin-bottom: 16px;

		@media (max-width:$md2+px) {
			height: 187px;
		}

		@media (max-width:$md5+px) {
			height: 276px;
		}

		img {
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			width: 100%;
			object-fit: cover;
			transition: all 0.8s ease 0s;
		}
	}

	&__name {
		color: $color__ocean;
		font-size: 16px;
		font-weight: 700;
		letter-spacing: 0.1px;
		margin-bottom: 2px;
	}

	&__list {}

	&__item {
		margin-bottom: 3px;
		color: $color__grey;
		font-size: 14px;
		font-weight: 400;
		letter-spacing: 0.25px;
	}
}