/*Обнуление*/
* {
	padding: 0;
	margin: 0;
	border: 0;
}

*,
*:before,
*:after {
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}

:focus,
:active {
	outline: none;
}

a:focus,
a:active {
	outline: none;
}

nav,
footer,
header,
aside {
	display: block;
}

html {
	// scroll-behavior: smooth;
}


html,
body {
	height: 100%;
	width: 100%;
	min-width: 320px;
	font-size: 100%;
	line-height: 1;
	font-size: 14px;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
	font-family: 'ProximaNova';
	color: $color__black;
}


body {
	&.lock {
		overflow: hidden;
	}
}

a {
	cursor: pointer;
	color: inherit;
}

input,
button,
textarea {
	font-family: inherit;
	font-size: 16px;
}

input::-ms-clear {
	display: none;
}

button {
	cursor: pointer;
	font-size: inherit;
	background: unset;
}

button::-moz-focus-inner {
	padding: 0;
	border: 0;
}

a,
a:visited {
	text-decoration: none;
}

ul li {
	list-style: none;
}

img {
	vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-size: inherit;
	font-weight: 400;
}
//===============================================================================================================================================================
.ibg {
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;

	img {
		display: none;
	}
}
.text-decoration{
	display: inline-block;
	position: relative;
	&:before{
		content:'';
		position:absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 1px;
		background-color: $color__black;
	}
}
.container {
	margin: 0 auto;
	position: relative;
	max-width: $maxWidthContainer+px;
	padding: 0 20px;
}
.wrapper {
	width: 100%;
	min-height: 100%;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	position: relative;
}


.page {
	flex: 1 1 auto;
}
.icon-spoller{
	width: 15px;
	height: 15px;
	background-color: $color__black-dark;
	border-radius: 50%;
	position: absolute;
	right: 20px;

	&:before {
		content: '';
		right: 3px;
		width: 9px;
		height: 2px;
		background-color: #fff;
		position: absolute;
		top: 6.5px;
		transition: all 0.3s ease 0s;
	}

	&:after {
		content: '';
		width: 9px;
		height: 2px;
		background-color: #fff;
		position: absolute;
		top: 6.5px;
		transform: rotate(90deg);
		right: 3px;
		transition: all 0.3s ease 0s;
	}
	&_arrow{
		border: solid transparent;
		height: 0;
		width: 0;
		position: absolute;
		right: 18px;
		top: 20px;
		border-top-color: $color__black;
		border-width: 5px;
		transition: all 0.3s ease 0s;
		&:before,
		&:after{
			display: none;
		}
	}
}
.title {
	font-size: 36px;
	font-weight: 700;
	letter-spacing: 0.23px;
	line-height: 27px;
	@media (max-width:$md2+px){
		font-size: 40px;
		letter-spacing: 0.26px;
		line-height: 42px;
	}

	&_big {
		color: $color__black-dark;
		font-size: 50px;
		letter-spacing: 0.32px;
		line-height: 45px;
		font-weight: 800;
		@media (max-width:$md5+px){
			 font-size: 36px; 
			 letter-spacing: 0.23px;
			 line-height: 36px;
		}
	}
	&_red{
		color: $color__red;
		font-size: 26px;
		letter-spacing: 0.17px;
		line-height: 29px;
	}
}
.btn {
	display: flex;
	justify-content: center;
	align-items: center;
	transition: all 0.3s ease 0s;
	color: #fff;
	position: relative;
	height: 60px;
	line-height: 26px;
	color: $color__black-dark;
	font-size: 16px;
	font-weight: 700;
	letter-spacing: 0.1px;
	white-space: nowrap;
	cursor: pointer;
	transition: all 0.3s ease 0s;
	&::after{
		content:'';
		position: absolute;
		right: 30px;
		top: 50%;
		transform: translateY(-50%);
		border: solid transparent;
		height: 0;
		width: 0;
		pointer-events: none;
		border-left-color: $color__black;
		border-width: 5px;
	}

	&_light{
		color: $color__black;
		background-color: $color__white;
		@media (any-hover: hover) {
			&:hover {
				background: $color__grey-lighter;
			}
		}
	}
	&_red{
		background-color: $color__red;
		color: $color__white;
		&::after {
			border-left-color: $color__white;
		}
		@media (any-hover: hover) {
			&:hover {
				background: $color__red-light;
			}
		}
	}
	&_load-more{
		font-weight: 400;
		letter-spacing: 0.1px;
		line-height: 20px;
		width: 100%;
		background-color: $color__ocean;
		color: $color__white;
		&:after{
			display: none;
		}
		@media (any-hover: hover){
			&:hover{
				background-color: $color__blue;
			}
		}
	}

	@media (max-width:$md2+px) {
		font-size: 14px;
	}
}
.input {
	width: 100%;
	height: 100%;
	display: block;
	position: relative;
	color: $color__primary;
	transition: all 0.3s ease 0s;

	&::-webkit-input-placeholder {
		color: $color__grey;
		transition: all 0.1s ease 0s;
	}

	&:focus::-webkit-input-placeholder {
		color: transparent;
	}
}
.search-block {
	padding: 15px 14px;
	background-color: $color__red;
	position: relative;

	@media (max-width:$md3+px) {
		padding: 15px 0;

		&:after,
		&:before {
			content: '';
			position: absolute;
			top: 0;
			background-color: $color__red;
			height: 100%;
			width: 100%;
		}

		&:before {
			left: 98%;
		}

		&:after {
			left: -98%;
		}
	}

	&.active {
		.search-form__item {
			opacity: 1;
			visibility: visible;
			transform: translateY(0%);
		}

		.search-form__button {
			&:before {
				color: $color__red;
			}
		}
	}

	&__item {
		position: relative;
		z-index: 2;
		background-color: $color__white;
		display: flex;
		align-items: center;
		border: 1px solid $color__grey-lighter;
		width: 100%;
		transition: all 0.3s ease 0s;
	}

	&__button {
		flex: 0 0 41px;
		height: 41px;
		border-left: 1px solid $color__grey-lighter;
		transition: all 0.3s ease 0s;

		&:before {
			font-size: 18px;
		}

		@media (any-hover: hover) {
			&:hover {
				background-color: $color__white-dark;
			}
		}
	}

	&__input {
		height: 41px;
		padding: 0 20px;

		&:focus {
			box-shadow: 0 0 0 0.2rem #bfdeff;
		}
	}
}
//===============================================================================================================================================================
._border-bottom{
	border-bottom: 1px solid $color__grey-lighter;
}
.spollers-block {
	@media (max-width:$md2+px) {
		max-width: 163px;
	}
	&__item {}

	&__header {
		height: 46px;
		display: flex;
		align-items: center;
		padding: 0 10px 0 16px;
		background-color: #f5f3f3;
		font-size: 13px;
		font-weight: 700;
		letter-spacing: 0.08px;
		cursor: pointer;

		position: relative;

		&.active {
			.icon-spoller {
				&:after {
					content: '';
					transform: rotate(90deg) scale(0);
					opacity: 0;
				}
				&_arrow{
					border-top-color: $color__red;
				}
			}
			
		}

		.icon-spoller {}
	}

	&__body {
		background-color: $color__white-dark;
		display: none;
	}

	&__link {
		display: block;
		font-size: 13px;
		font-weight: 700;
		letter-spacing: 0.08px;
		padding: 15px 20px;
		transition: all 0.3s ease 0s;
		background-color: #fbfbfb;
		span{
			padding-bottom: 1px;
		}
		@media (any-hover: hover) {
			&:hover {
				text-decoration: underline;
			}
		}
		@media (max-width:$md2+px){
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
		}
	}
}


//===============================================================================================================================================================

.info-main{
	padding-bottom: 30px;
	margin-bottom: 35px;
	border-bottom: 1px solid $color__grey-lighter;

	@media (max-width:$md3+px){
		padding-bottom: 20px;
		margin-bottom: 18px;
	}
	&:last-child {
		margin-bottom: 0;
	}
	&__header{
		display: flex;
		flex-wrap: wrap;
		margin-bottom: 4px;
	}
	&__item{
		display: inline-block;
		color: $color__grey-dark;
		font-size: 18px;
		font-weight: 700;
		letter-spacing: 0.12px;
		line-height: 26px;
		margin-right: 28px;
		margin-bottom: 2px;
		@media (max-width:$md5+px){
			margin-right: 20px;
		}
		@media (max-width:$md6+px) {
			margin-right: 14px;
		}
		&:last-child{
			margin-right: 0;
			@media (max-width:$md5+px){
				 display: none; 
			}
		}
	}
	&__link{
		color: $color__ocean;
		font-size: 16px;
		font-weight: 600;
		letter-spacing: 0.1px;
		margin-bottom: 15px;
		display: flex;
		align-items: center;
		line-height: 24px;
		&_pdf{
			&:before {
				content: '';
				margin-right: 13px;
				width: 19px;
				height: 23px;
				background-image: url('../img/icons/pdf.svg');
			}
		}
		&_sound{
			&:before {
				content: '';
				margin-right: 15px;
				width: 20px;
				height: 22px;
				background-image: url('../img/icons/sound.svg');
				@media (max-width:$md2+px){
					margin-right: 10px;
				}
			}
		}
		&:last-child{
			margin-bottom: 0px;
		}
	}
}
//===============================================================================================================================================================

.content-block {
	padding-top: 200px;
	padding-bottom: 46px;

	@media (max-width:$md2+px) {
		padding-top: 117px;
	}

	&__row {
		display: flex;
	}

	&__side {
		flex: 0 0 263px;
		@media (max-width:$md2+px){
			flex: 0 0 163px;
		}
		@media (max-width:$md3+px) {
			display: none;
		}
	}

	&__main {
		flex: 1 1 auto;
		padding-left: 27px;

		@media (max-width:$md3+px) {
			padding-left: 0;
		}
	}

}

.main-content {
	&__header {
		margin-bottom: 29px;

		@media (min-width:$md2+px) {
			display: flex;
			margin-bottom: 40px;
		}

		@media (max-width:$md3+px) {
			margin-bottom: 36px;
		}
	}

	&__body {
		margin-bottom: 15px;
	}

	&__column {
		margin-bottom: 85px;

		&:last-child {
			margin-bottom: 0;
		}

		@media (max-width:$md5+px) {
			margin-bottom: 85px;
		}
	}

	&__title {
		margin-bottom: 22px;
		font-size: 40px;
		letter-spacing: 0.26px;
		line-height: 44px;
		font-weight: 800;

		@media (max-width:$md5+px) {
			flex: 1 1 auto;
		}

		@media (max-width:$md7+px) {
			font-size: 36px;
		}
	}

	&__text {
		font-size: 18px;
		font-weight: 300;
		letter-spacing: 0.12px;
		line-height: 26px;

		@media (max-width:$md3+px) {
			max-width: 483px;
		}
	}

	&__sub-title {
		color: $color__black-dark;
		font-size: 24px;
		font-weight: 700;
		letter-spacing: 0.16px;
		line-height: 27px;
		margin-bottom: 7px;
	}

	&__spollers {
		margin-bottom: 35px;
		max-width: 220px;
		display: none;
		@media (max-width:$md3+px) {
			display: block;
		}
	}
	&__pagging {
		margin-top: 30px;
	}
}
//===============================================================================================================================================================

.main-filter{
	display: flex;
	flex-wrap: wrap;
	margin-bottom: 50px;
	@media (max-width:$md3+px) {
		margin-bottom: 22px;
	}
	&__item{
		cursor: pointer;
		margin-bottom: 9px;
	}
	&__link{
		display: block;
		color: $color__ocean;
		font-size: 16px;
		font-weight: 700;
		letter-spacing: 0.1px;
		position: relative;
		padding: 0 20px;
		&::before {
			content: '';
			position: absolute;
			left: 0px;
			top: 50%;
			transform: translateY(-2px);
			border: solid transparent;
			height: 0;
			width: 0;
			pointer-events: none;
			border-top-color: $color__red;
			border-width: 5px 4px 0 5px;
		}
	}
}
//===============================================================================================================================================================

.video-card{

	&__body{
		background-color: #f5f3f3;
	}
	&__video{
		position: relative;
		padding-bottom: 50.3%;

		iframe{
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			width: 100%;
			object-fit: cover;
		}
	}
	&__content{
		padding: 16px 21px 23px 21px;
	}

	&__sub-title{
		display: inline-block;
		color: $color__ocean;
		font-size: 14px;
		font-weight: 700;
		letter-spacing: 0.09px;
		margin-bottom: 10px;
	}
	&__title{
		color: $color__grey;
		font-size: 20px;
		font-weight: 700;
		letter-spacing: 0.13px;
		line-height: 26px;
		margin-bottom: 10px;
	}
	&__text{
		margin-bottom: 20px;
		color: $color__grey;
		font-size: 14px;
		font-weight: 300;
		letter-spacing: 0.09px;
		line-height: 18px;
		span{
			display: block;
		}
	}
	&__link{
		color: $color__red;
		font-size: 14px;
		font-weight: 700;
		letter-spacing: 0.09px;
		&:before{
			background-color: $color__red;
			transition: all 0.5s ease 0s;
		}
		@media (any-hover: hover){
			&:hover{
				&:before {
					transform: scaleX(0);
				}
			}
		}
	}
}