.pagging {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	@media (max-width:$md5+px) {
		justify-content: center;
	}
	&__arrow{
		margin-bottom: 2px;
		border: solid transparent;
		height: 0;
		width: 0;	
		border-width: 5px;
		transition: all 0.3s ease 0s;
		&_prev{
			border-right-color: $color__grey-lighter;
			@media (any-hover: hover){
				&:hover {
					border-right-color: $color__grey-dark;
				}
			}
		}

		&_next{
			border-left-color: $color__grey-lighter;
			@media (any-hover: hover){
				&:hover{
					border-left-color: $color__grey-dark;
				}
			}
		}
		@media (any-hover: hover){
			&:hover{

				&_next {
					border-left-color: red;
				}
			}
		}
	}
	// .pagging__list
	&__list {
		display: flex;
		flex-wrap: wrap;
		@media (max-width:$md4+px){
			justify-content: center; 
		}
		li {
			margin-right: 20px;
			@media (max-width:$md2+px){
				margin-right: 18px;
			}
			@media (max-width:$md5+px){
				 margin-right: 26px; 
			}
			&:last-child{
				margin-right: 0;
			}
		}
	}

	// .pagging__item
	&__item {
		color: $color__ocean;
		font-size: 16px;
		font-weight: 600;
		letter-spacing: 0.1px;
		margin-bottom: 2px;
		display: block;
		position: relative;
		&.active {
			color: $color__blue;
		}
	}
}
