.header{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 10;
	background-color: $color__white;
	transition: all 0.3s ease 0s;
	
	&__top{
		@media (min-width:$md2+px){
			padding: 11px 0;
		}
	}
} 
.top-header{
	background-color: $color__white-dark;
	&__container{
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	&__logo {
		@media (max-width:$md2+px){
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
		}
		position: relative;
		width: 140px;
		height: 39px;
		z-index: 5;
		transition: all 0.3s ease 0s;

		img {
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			width: 100%;
		}
	}
	&__main{
		display: flex;
		align-items: center;
	}
	&__title{
		font-weight: 700;
		letter-spacing: 0.09px;
		margin-left: 32px;
		@media (max-width:$md2+px){
			display: none;
		}
	}
}


.actions-top-header{
	position: relative;
	z-index: 5;
	display: flex;
	align-items: center;
	&__item{
		display: block;
		color: $color__ocean;
		padding: 0 17px;
		border-left: 1px solid $color__black-dark;
		transition: all 0.3s ease 0s;
		&:first-child{
			border-left: none;
		}
		@media (max-width:$md2+px){
			border-left: unset;
			padding: 0;
			margin-right: 17px;
			span {
				display: none;
			}
			&.login {
				background-image: url('../img/icons/Login.svg');
				background-repeat: no-repeat;
				background-position: center;
				background-size: contain;
				width: 28px;
				height: 23px;
			}
		}
		@media (any-hover: hover) {
			&:hover {
				color: $color__red;
			}
		}
	}
	&__search-button{
		position: relative;
		z-index: 5;
		height: 18px;
		width: 18px;
		&:before{
			font-size: 18px;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		@media (min-width:$md2+px){
			display: none;
		}
		&.active{
			color: $color__red;
		}
	}
	
}
//===============================================================================================================================================================
.menu {
	@media (max-width:$md2+px) {
		&::after {
			content: '';
			position: absolute;
			top: -63px;
			left: 0;
			background-color: #fff;
			height: 63px;
			width: 100%;
			z-index: 4;
			transition: all 0.3s ease 0s;
		}
	}

	&__body {
		@media (max-width:$md2+px) {
			background: #fff;
			position: fixed;
			width: 100%;
			max-width: 320px;
			height: 100%;
			left: 0;
			top: 0;
			overflow: auto;
			padding-top: 63px;
			transition: all 0.3s ease;
			z-index: 3;
			display: flex;
			flex-direction: column;
			transform: translateX(-100%);

			&.active {
				transform: translateX(0%);
				opacity: 1;
				visibility: visible;
			}
		}
		@media (max-width:$md6+px){
			max-width: 100%;
		}
	}

	&__list {
		@media (min-width:$md2+px){
			display: flex;
			align-items: center;
			justify-content: space-evenly;
		}
	}

	&__item {
		position: relative;
		width: 100%;

		@media (min-width:$md2+px){
			border-left: 1px solid $color__grey-lighter;

			&:last-child {
				border-right: 1px solid $color__grey-lighter;
			}
			&.active {
				.menu__sub-list {
					z-index: 2;
				}
			}
		}
		@media (max-width:$md2+px){
			border-bottom: 1px solid $color__grey-lighter;

			&:last-child {
				border-bottom: 1px solid $color__grey-lighter;
			}
		}
	}

	&__link {
		position: relative;
		font-weight: 700;
		padding: 0 15px;
		height: 60px;
		display: flex;
		align-items: center;
		font-size: 16px;
		transition: all 0.3s ease 0s;
		&.this-page{
			@media (min-width:$md2+px){
				background-color: $color__red;
				color: $color__white;
			}
		}
		&.active{
			background-color: $color__ocean;
			color: $color__white;
			&:after {
				opacity: 1;
			}
			.menu__icon-spoller{
				@media (min-width:$md3+px) {
					&:before {
						border-color: $color__white transparent transparent transparent;
					}
					&:after{
						opacity: 1;
					}
				}
				@media (max-width:$md2+px){
					background-color: $color__white;
					&:after{
						content:'';
						transform: rotate(90deg) scale(0);
						opacity: 0;
					}
					&:before{
						background-color: $color__ocean;
					}
				}
			}
		}
		@media (min-width:$md2+px){
			font-size: 14px;
			height: 83px;
			justify-content: center;
			
			&:hover {
				background-color: $color__red;
				color: $color__white;
				&:after {
					opacity: 1;
					background-color: $color__red-dark;
				}
				.menu__icon-spoller {
					&:before {
						border-color: $color__white transparent transparent transparent;
					}
				}
				&.active {
					background-color: $color__ocean;
					&:after {
						background-color: $color__blue-dark;
					}
					.menu__icon-spoller {
						&:before {
							border-color: $color__white transparent transparent transparent;
						}
					}
				}
			}
			&:after {
				content: '';
				opacity: 0;
				position: absolute;
				width: 100%;
				height: 10px;
				bottom: 0;
				left: 0;
				background-color: $color__blue-dark;
				transition: all 0.3s ease 0s;
			}
		}

	}
	&__icon-spoller{
		
		@media (min-width:$md2+px){
			 &:before {
			 	content: '';
			 	position: absolute;
			 	bottom: 23px;
			 	left: 50%;
			 	transform: translateX(-50%);
			 	width: 0;
			 	height: 0;
			 	border-style: solid;
			 	border-width: 5px 5px 0 5px;
			 	border-color: $color__red transparent transparent transparent;
				 transition: all 0.3s ease 0s;
			 }
		}
		@media (max-width:$md2+px){
			width: 15px;
			height: 15px;
			background-color: $color__ocean;
			border-radius: 50%;
			position: absolute;
			right: 20px;
			 &:before {
			 	content: '';
			 	right: 3px;
			 	width: 9px;
			 	height: 2px;
			 	background-color: #fff;
			 	position: absolute;
			 	top: 6.5px;
			 	transition: all 0.3s ease 0s;
			 }

			 &:after {
			 	content: '';
			 	content: '';
			 	width: 9px;
			 	height: 2px;
			 	background-color: #fff;
			 	position: absolute;
			 	top: 6.5px;
			 	transform: rotate(90deg);
			 	right: 3px;
			 	transition: all 0.3s ease 0s;
			 }
		}
	}

	&__sub-list{
		position: relative;
		outline: 1px solid $color__white;
		flex: 1 1 auto;
		display: none;
		background-color: $color__ocean;
		@media (min-width:$md2+px){
			min-width: 267px;
			position: absolute;
			top: 100%;
			left: 0;
		}
	}
	&__sub-item{
		border-bottom: 1px solid $color__white;
		&:last-child {
			border-bottom: unset;
		}
	}
	&__sub-link{
		display: block;
		color: $color__white;
		font-size: 15px;
		font-weight: 500;
		letter-spacing: 0.09px;
		padding: 22px 20px 22px 25px;
		transition: all 0.3s ease 0s;
		letter-spacing: 0.1px;
		&:hover{
			background-color: $color__blue-dark;
		}
		@media (min-width:$md2+px){
			font-size: 14px;
			padding: 22px 20px 22px 35px;
		}
	}
}

.icon-menu {
	display: none;
	width: 63px;
	height: 63px;
	cursor: pointer;
	z-index: 5;
	background-color: $color__ocean;
	position: relative;
	left: -20px;
	&__header{
		color: $color__white;
		margin-bottom: 3px;
		&:first-child {
			display: block;
		}
		&:nth-child(2) {
			display: none;
		}
	}
	&__body {
		width: 28px;
		height: 19px;
		position: relative;
		left: 50%;
		transform: translateX(-50%);
	}

	span {
		width: 100%;
		height: 2px;
		border-radius: 3px;
		background-color: #fff;
		position: absolute;
		top: 9px;
		left: 0;

		&:first-child {
			top: 1px;
			transition: all 0.3s ease;
		}

		&:last-child {
			top: auto;
			bottom: 0px;
			transition: all 0.3s ease;
		}
	}

	&.active {
		background-color: $color__red;
		.icon-menu__header {
			color: $color__white;
			margin-bottom: 3px;

			&:first-child {
				display: none;
			}
			
			&:nth-child(2) {
				display: block;
			}
		}
		span {
			transform: scale(0);
			background-color: #fff;

			&:first-child {
				transform: rotate(-45deg);
				top: 8px;
			}

			&:last-child {
				transform: rotate(45deg);
				bottom: 9px;
			}
		}
	}

	@media (max-width:$md2+px) {
		display: block;
	}
}
.bottom-header {
	@media (min-width:$md2+px){
		border-top: 1px solid $color__grey-lighter;
		border-bottom: 1px solid $color__grey-lighter;
	}
	&__container {
		display: flex;
		align-items: center;
	}

	&__menu {
		flex: 0 0 826/1130 * 100%;
	}
	&__search{
		@media (min-width:$md2+px){
			flex: 1 1 auto;
			display: flex;
			justify-content: flex-end;
			padding: 0 0px 0 15px;
		}
	}
}
.search-form{
	max-width: 259px;
	width: 100%;

	&.active{
		.search-form__item{
			opacity: 1;
			visibility: visible;
			transform: translateY(0%);
		}
		.search-form__button{
			&:before{
				color: $color__red;
			}
		}
	}
	&__item{
		background-color: $color__white;
		display: flex;
		align-items: center;
		border: 1px solid $color__grey-lighter;
		@media (max-width:$md2+px){
			position: absolute;
			width: 100%;
			left: 0;
			transform: translateY(-100%);
			top: 0;
			z-index: 3;
			opacity: 0;
			visibility: hidden;
			transition: all 0.3s ease 0s;
			box-shadow: 0 0 5px rgba(212, 209, 205, 0.3);
		}
	}

	&__button{
		flex: 0 0 41px;
		height: 41px;
		border-left: 1px solid $color__grey-lighter;
		transition: all 0.3s ease 0s;
		&:before{
			font-size: 18px;
		}
		@media (any-hover: hover) {
			&:hover{
				background-color: $color__white-dark;
			}
		}
	}
	&__input{
		height: 41px;
		padding: 0 20px;
		&:focus{
			box-shadow: 0 0 0 0.2rem #bfdeff;
		}

	}
}
