// //include third-party fonts

@mixin font-face($font-name, $file-name, $font-weight, $font-style) {
  @font-face {
    font-family: quote($font-name);
    src: url("../fonts/"+ $file-name + ".eot"),
    url("../fonts/"+ $file-name + ".woff") format("woff"),
	url("../fonts/"+ $file-name + ".ttf") format("truetype"),
	url("../fonts/"+ $file-name + ".svg") format("svg");
    font-weight: $font-weight;
    font-style: $font-style;
    font-display: swap;
  }
}


@include font-face(ProximaNova, ProximaNova-Black, 900, normal);
@include font-face(ProximaNova, Proxima Nova Extrabold, 800, normal);
@include font-face(ProximaNova, ProximaNova-Bold, 700, normal);
@include font-face(ProximaNova, Proxima Nova Semibold, 600, normal);
@include font-face(ProximaNova, ProximaNova-Medium, 500, normal);
@include font-face(ProximaNova, ProximaNova-Regular, normal, normal);
@include font-face(ProximaNova, Proxima Nova Light, 300, normal);
@include font-face(ProximaNova, ProximaNova-Thin, 100, normal);
