@font-face {
	font-family: 'icomoon';
	src: url('../fonts/icomoon.eot?ae40z9');
	src: url('../fonts/icomoon.eot?ae40z9#iefix') format('embedded-opentype'),
		url('../fonts/icomoon.ttf?ae40z9') format('truetype'),
		url('../fonts/icomoon.woff?ae40z9') format('woff'),
		url('../fonts/icomoon.svg?ae40z9#icomoon') format('svg');
	font-weight: normal;
	font-style: normal;
	font-display: block;
}

[class^="icon-"]::before,
[class*=" icon-"]::before {
	/* use !important to prevent issues with browser extensions that change fonts */
	font-family: 'icomoon' !important;
	speak: never;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;

	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.icon-search:before {
	content: "\e905";
}

.icon-arrow:before {
	content: "\e902";
}
