.latest-releases{
	margin-bottom: 48px;
	@media (max-width:$md2+px){
		margin-bottom: 46px;
	}
	&__row{
		@media (min-width:$md2+px){
			 display: flex; 
		}
	}
	&__column{
		flex: 1 1 auto;
		&:first-child{
			flex: 0 0 483/1130 * 100%;
		}
	}

	&__title{
		margin-bottom: 82px;
		@media (max-width:$md2+px){
			margin-bottom: 22px;
		}
	}
	&__spollers{
		max-width: 263px;
		margin-bottom: 31px;
		@media (max-width:$md2+px){
			display: none;
		}
	}
}


.mediathek-block{
	margin-bottom: 50px;
	position: relative;

	&__container{
		@media (min-width:$md4+px){
			justify-content: flex-end;
		}
		display: flex;
		justify-content: center;
	}
	&__content{
		flex: 0 0 499/1130 * 100%;
		padding: 70px 0;
		@media (max-width:$md2+px){
			flex: 0 1 435px;
			padding: 134px 0;
		}
		@media (max-width:$md5+px){
			 flex: 0 0 289px;
		}
	}
	&__bg{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
	&__text{
		font-style: italic;
		margin-bottom: 38px;
		font-size: 36px;
		font-weight: 500;
		color: $color__black-dark;
		letter-spacing: 0.23px;
		line-height: 42px;
		text-align: center;
		@media (max-width:$md2+px){
			margin-bottom: 29px;
		}
		@media (max-width:$md5+px){
			font-size: 24px;
			font-weight: 500;
			letter-spacing: 0.16px;
			line-height: 28px;
		}
	}
	&__button{
		max-width: 290px;
		margin: 0 auto;
		@media (max-width:$md2+px){
			max-width: 280px;
		}
	}
}

.info-block {
	&__header {
		font-size: 18px;
		font-weight: 300;
		font-style: normal;
		letter-spacing: 0.12px;
		line-height: 26px;
		margin-bottom: 32px;
	}

	&__title {
		font-size: 26px;
		font-weight: 700;
		color: $color__red;
		letter-spacing: 0.17px;
		line-height: 27px;
		margin-bottom: 21px;
	}

	&__spollers {
		max-width: 163px;
		margin-bottom: 31px;
		display: none;

		@media (max-width:$md2+px) {
			display: block;
		}
	}

	&__item {
		margin-bottom: 29px;

		&:last-child {
			margin-bottom: 0;
		}
	}

	&__info {
		display: flex;
		margin-bottom: 5px;
	}

	&__category {
		margin-left: 20px;
	}

	&__data,
	&__category {
		font-size: 14px;
		font-weight: 700;
		color: $color__black-dark;
		letter-spacing: 0.09px;
	}

	&__text {
		color: $color__grey;
		font-size: 20px;
		font-weight: 500;
		letter-spacing: 0.13px;

		@media (max-width:$md2+px) {
			font-size: 18px;
			font-weight: 300;
			letter-spacing: 0.05px;
			line-height: 20px;
		}
	}
}
