.footer{
	width: 100%;
	position: relative;
	z-index: 2;
	background-color: $color__grey-dark;
	@media (max-width:$md3+px){
		outline: 1px solid #B8B8B8;
	}
	&__row{
		@media (min-width:$md3+px){
			display: flex;
		}
	}
	&__main{
		position: relative;
		background-color: $color__white;
		flex: 1 1 auto;
		padding: 18px 0px 16px 0px;
		@media (min-width:$md3+px){
			padding: 120px 20px 96px 20px;
			flex: 0 0 263/1130 * 100%;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
		}
		@media (max-width:$md2+px){
			flex: 0 0 263/1170 * 100%;
		}
		@media (max-width:$md3+px){
			&:after,
			&:before{
				content:'';
				background-color: $color__white;
				position: absolute;
				top: 0;
				width: 100%;
				height: 100%;
			}
			&:after{
				left: 98%;
			}
			&:before{
				content:'';
				right: 98%;
			}
		}
	}
	&__logo {
		position: relative;
		display: block;
		margin-bottom: 18px;
		width: 117px;
		height: 33px;
		z-index: 2;
		img{
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			width: 100%;
			object-fit: contain;
		}
		@media (min-width:$md3+px){
			margin-bottom: 20px;
		}
	}
	&__info{
		position: relative;
		color: $color__grey-dark;
		font-size: 12px;
		font-weight: 300;
		letter-spacing: 0.03px;
		line-height: 15px;
		z-index: 2;
		@media (min-width:$md3+px){
			text-align: center;
			font-weight: 400;
			letter-spacing: 0.08px;
		}
		span{
			@media (max-width:$md2+px){
				display: none;
			}
		}
	}
}
.menu-footer{
	position: relative;
	@media (min-width:$md3+px){
		flex: 1 1 auto;
		display: flex; 
		padding: 20px 10px;
	}
	&__column{
		flex: 0 1 25%;
		position: relative;
		padding: 17px 0px;
		
		&:before{
			content:'';
			position: absolute;
			right: 0;
			bottom: 0;
			width: 100%;
			height: 1px;
			background-color: $color__white;
		}
		@media (min-width:$md3+px) {
			padding: 17px 15px;
			&:before {
				content: '';
				right: 0;
				top: 0;
				bottom: auto;
				width: 1px;
				height: 100%;
			}
		}
		&:last-child{
			&:before{
				display: none;
			}
		}
	}
	&__title{
		display: block;
		font-size: 13px;
		font-weight: 700;
		letter-spacing: 0.08px;
		color: $color__white;
		text-transform: uppercase;
		margin-bottom: 20px;
	}
	&__list{
		li{
			margin-bottom: 20px;

			&:last-child {
				margin-bottom: 0;
			}
		}
	}
	&__link{
		display: block;
		color: $color__white;
		font-size: 14px;
		font-weight: 500;
		letter-spacing: 0.09px;
		text-decoration: underline;
		line-height: 150%;
		transition: all 0.3s ease 0s;
		@media (any-hover: hover) {
			&:hover {
				font-style: italic;
			}
		}
	}
}