.suche {
	padding-top: 200px;
	padding-bottom: 46px;

	@media (max-width:$md2+px) {
		padding-top: 117px;
	}
	@media (max-width:$md3+px){
		 padding-top: 87px; 
	}

	&__row {
		display: flex;
		@media (max-width:$md3+px){
			 display: block; 
		}
	}

	&__side {
		flex: 0 0 263px;
		@media (max-width:$md3+px){
			 margin-bottom: 49px; 
		}
		
	}

	&__main {
		flex: 1 1 auto;
		padding-left: 27px;

		@media (max-width:$md3+px) {
			padding-left: 0;
		}
	}
	&__title {
		margin-bottom: 22px;
		font-size: 40px;
		letter-spacing: 0.26px;
		line-height: 44px;
		position: relative;
		
		@media (min-width:$md3+px){
			padding-bottom: 33px;
			 &:before{
				content:'';
				position: absolute;
				bottom: 0;
				left: 0;
				height: 1px;
				width: 100%;
				background-color: $color__grey-lighter;
			 } 
		}
		@media (max-width:$md5+px) {
			flex: 1 1 auto;
		}
		@media (max-width:$md7+px) {
			font-size: 36px;
		}
	}
	&__sub-title {
		color: $color__black-dark;
		font-size: 16px;
		font-weight: 600;
		font-style: normal;
		letter-spacing: 0.1px;
		line-height: 22px;
	}
}
.main-suche {
	&__header {
		margin-bottom: 30px;
	}
	&__text {
		max-width: 558px;
		color: $color__black-dark;
		font-size: 18px;
		font-weight: 500;
		letter-spacing: 0.12px;
		margin-bottom: 40px;
	}
	&__button{
		max-width: 248px;
		@media (max-width:$md2+px){
			max-width: 238px;
		}
		@media (max-width:$md4+px){
			max-width: 159px;
			justify-content: flex-start;
			padding-left: 20px;
			&:after{
				right: 20px;
			}
		}
	}
	&__search{
		margin-bottom: 25px;
		@media (max-width:$md3+px){
			 margin-bottom: 34px; 
		}
	}
	.info-main__item{
		font-size: 14px;
		letter-spacing: 0.09px;
		margin-right: 32px;
		@media (max-width:$md2+px){
			margin-right: 22px;
		}
		&:last-child {
			margin-right: 0;
		}
	}
	&__footer{
		margin-top: 50px;
		@media (max-width:$md2+px){
			margin-top: 30px;
		}
	}
	&__pagging {
		display: flex;
		@media (max-width:$md3+px) {
			display: none;
		}
		.pagging__list{
			li{
				margin-right: 0;
				&:last-child{
					.pagging__item{
						&:before {
							display: none;
						}
					}
				}
			}
		}
		.pagging__item{
			&.active{
				color: $color__red;
			}
			padding: 0 10px;
			&:before {
				content: '';
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				right: 0.5px;
				height: 80%;
				width: 1px;
				background-color: $color__black-dark;
			}

			
		}
	}

	&__load-more{
		display: none;
		@media (max-width:$md3+px){
			 display: block; 
		}
	}
}
