.icon-spoller {
  width: 15px;
  height: 15px;
  background-color: #212529;
  border-radius: 50%;
  position: absolute;
  right: 20px;
}

.icon-spoller:before {
  content: '';
  right: 3px;
  width: 9px;
  height: 2px;
  background-color: #fff;
  position: absolute;
  top: 6.5px;
  transition: all 0.3s ease 0s;
}

.icon-spoller:after {
  content: '';
  width: 9px;
  height: 2px;
  background-color: #fff;
  position: absolute;
  top: 6.5px;
  transform: rotate(90deg);
  right: 3px;
  transition: all 0.3s ease 0s;
}

.icon-spoller_arrow {
  border: solid transparent;
  height: 0;
  width: 0;
  position: absolute;
  right: 18px;
  top: 20px;
  border-top-color: #000000;
  border-width: 5px;
  transition: all 0.3s ease 0s;
}

.icon-spoller_arrow:before, .icon-spoller_arrow:after {
  display: none;
}

